'use strict';

var titleize = function titleize(input) {
  if (typeof input !== 'string') {
    throw new TypeError('Expected a string');
  }

  return input.toLowerCase().replace(/(?:^|\s|-)\S/g, function (x) {
    return x.toUpperCase();
  });
};

module.exports = titleize; // TODO: Remove this for the next major release

module.exports.default = titleize;